var _ = require('./util');


/**
 * è§£æstyle
 *
 * @param {String} css
 * @param {Function} onAttr å¤çå±æ§çå½æ°
 *   åæ°æ ¼å¼ï¼ function (sourcePosition, position, name, value, source)
 * @return {String}
 */
function parseStyle (css, onAttr) {
  css = _.trimRight(css);
  if (css[css.length - 1] !== ';') css += ';';
  var cssLength = css.length;
  var isParenthesisOpen = false;
  var lastPos = 0;
  var i = 0;
  var retCSS = '';

  function addNewAttr () {
    // å¦ææ²¡ææ­£å¸¸çé­ååæ¬å·ï¼åç´æ¥å¿½ç¥å½åå±æ§
    if (!isParenthesisOpen) {
      var source = _.trim(css.slice(lastPos, i));
      var j = source.indexOf(':');
      if (j !== -1) {
        var name = _.trim(source.slice(0, j));
        var value = _.trim(source.slice(j + 1));
        // å¿é¡»æå±æ§åç§°
        if (name) {
          var ret = onAttr(lastPos, retCSS.length, name, value, source);
          if (ret) retCSS += ret + '; ';
        }
      }
    }
    lastPos = i + 1;
  }

  for (; i < cssLength; i++) {
    var c = css[i];
    if (c === '/' && css[i + 1] === '*') {
      // å¤æ³¨å¼å§
      var j = css.indexOf('*/', i + 2);
      // å¦ææ²¡ææ­£å¸¸çå¤æ³¨ç»æï¼ååé¢çé¨åå¨é¨è·³è¿
      if (j === -1) break;
      // ç´æ¥å°å½åä½ç½®è°å°å¤æ³¨ç»å°¾ï¼å¹¶ä¸åå§åç¶æ
      i = j + 1;
      lastPos = i + 1;
      isParenthesisOpen = false;
    } else if (c === '(') {
      isParenthesisOpen = true;
    } else if (c === ')') {
      isParenthesisOpen = false;
    } else if (c === ';') {
      if (isParenthesisOpen) {
        // å¨åæ¬å·éé¢ï¼å¿½ç¥
      } else {
        addNewAttr();
      }
    } else if (c === '\n') {
      addNewAttr();
    }
  }

  return _.trim(retCSS);
}

module.exports = parseStyle;
